<template lang="pug">
  .panel.information
    .unit_basic
      .leftside
        .icon_basic
        .customername
          .name {{ customer_name }}
          .male {{ data.gender === 'F' ? '' : '' }}
        .btn_complain#lnkIndexComplain(
          :class="data.complaint > 0 ? '' : 'off'"
          @click="changeSection(3)"
        )
          span.big {{ data.complaint }}
          span.small 
          br
          span 

      .rightside
        .watermark {{ teller_id }}
        .title 
        .content
          ul
            li(v-if="data.birthday")
              |{{ new Date().getFullYear() - new Date(`${data.birthday}`).getFullYear() - 1 }} {{ new Date(`${data.birthday}`).getMonth() + 1 }}
            li(v-if="data.have_any_children") 
            li(v-if="data.person_in_charge") 
            li(v-if="data.account_type")
              span.green 
            li(v-if="data.annual_income && can_market")
              span.green 
              span.smallgray {{ data.annual_income_date }}
              br
              |{{ data.annual_income }}

    .unit_info
      .leftside
        .icon_info
        .btn_vip#lnkIndexVip(
          v-if="data.vip_status"
          @click="changeSection(4)"
        )
          |VIP
          br
          |{{ data.vip_status }}
        .btn_failcontact(v-if="data.availability == false")
          |-
          br
          |
        .notvip(v-if="!data.vip_notation")
          |<br>VIP

      .rightside
        .watermark {{ teller_id }}
        .title 
        .content
          ul
            li(v-if="data.financial_advisor_branch")
              |{{ data.financial_advisor_branch }}
            li(v-if="data.vip_notation")
              span.green {{ data.vip_notation != "V" ? data.vip_notation : '' }}VIP
            li(v-if="data.financial_advisor_series")
              span.green 
              br
              |{{ data.financial_advisor_name }} 
              span.smallgray {{ data.financial_advisor_series }}
      
</template>

<script>

export default {
  name: 'Information',
  data(){
    return {
      
    }
  },
  computed: {
    ...Vuex.mapState({
      loadingShow: state => state.loadingShow,
      data: state => state.information.dataset,
      can_market: state => state.recommend.data.can_market,
      customer_name: state => state.customer_name,
      teller_id: state => state.teller_id,
    }),
  },
  beforeMount(){
    this.getInformationData({
      path: 'information',
      title: ['COMPLAIN','VIP'],
    })
  },
  mounted(){
    
  },
  methods:{
    ...Vuex.mapMutations('nav',['changeSection']),
    ...Vuex.mapActions('information',['getInformationData']),
  },
  watch:{
    
  },
  destroyed(){
    
  },
  components:{

  }
}
</script>

<style lang="scss" scoped>
@import "./css/index/component/information.scss";
</style>



